<template>
  <div ref="agentPower">
    <!--<a href="javascript:void(0)" class="save"  @click="addMeal"><span class="add">+</span>添加套餐</a>-->
    <Button icon="ios-plus-empty" type="dashed" size="small" @click.native="addMeal" v-if="mealList.length !== 3">添加套餐</Button>
    <section class="mealList">
      <Tag v-for="(item,key) in mealList" :key="key" :name="key" closable @on-close="handleCloseMeal(key,item)" type="border" @click.native="seeMealInfo(item)"><span class="meal_tag">{{ item.type | mealName}}</span></Tag>
    </section>
    <Modal
      v-model="mealForm"
      title="添加代理商套餐"
      width="750"
    >
      <form>
        <p>
          <span><span class="must">*</span> 小程序类型 <strong>:</strong></span>
          <select v-model="type" class="select_type" v-if="flag">
            <option :value="item" v-for="(item,key) in mealSort">{{item | nameFilter}}</option>
          </select>
          <strong v-if="!flag">{{mealType | nameFilter}}</strong>
        </p>
        <p v-for="(item,key) in items" v-if="key !== 3">
          <span><span class="must">*</span> {{item.name}} <strong>:</strong></span>
          <input type="text" :placeholder="item.place" v-model="item.modal" @focus="enter(key)" @blur="out(key)" @input="input(key)" class="memberInput">
          <strong v-if="item.show"><!--<Icon type="close-circled" color="#e46751" size="20" style="margin-right: 0.05rem"></Icon>--> {{item.error}} </strong>
          <strong v-if="item.right"> <Icon type="checkmark-circled" size="20" color="green"></Icon> </strong>
        </p>
        <p class="btn">
          <a href="javascript:void(0)" class="save" @click="save" >保存</a>
          <a href="javascript:void(0)" @click="cancel"  class="cancel">取消</a>
        </p>
      </form>
      <div slot="footer">
      </div>
    </Modal>
  </div>
</template>
<script>
  import bread from '../bread.vue'
  export default {
    data () {
      return {
        mealType: 0,
//        添加/修改
        flag:true,
        mealForm: false,
        show:false,
        items:[
          {
            name:"小程序价格",
            place:"请输入小程序价格",
            error:'请输入小程序价格',
            modal:'',
            show:false,
            right:false
          },
          {
            name:"小程序时间",
            place:"整数年",
            error:'小程序到期时间',
            modal:'',
            show:false,
            right:false
          },
//        {
//          name:"小程序数量",
//          place:"小程序数量",
//          error:'小程序数量',
//          modal:'',
//          show:false,
//          right:false
//        }
        ],
        type:"1",
        id:-1
      }
    },
    methods: {
//      see mealInfo
      seeMealInfo (item) {
        this.id = item.id;
        this.flag = false;
        this.post("system/Agency/get_package_detail",{
          id: item.id
        }).then(res => {
          if (res.data.code === 10000) {
            this.mealType = res.data.data.type;
            this.items[0].modal = res.data.data.price;
            this.items[1].modal = res.data.data.year_num;
//          this.items[2].modal = res.data.data.app_num;
            this.items.splice(0,1, this.items[0]);
          } else {

          }
        })
        this.mealForm = true;
      },
//      close meal
      handleCloseMeal (key,item) {
        this.$Modal.confirm({
          title: '是否删除',
          content: '<p>确定要删除该套餐吗</p>',
          onOk: () => {
            this.post("system/Agency/delete_detail", {
              id: item.id,
            }).then(res => {
              if (res.data.code === 10000) {
                this.mealList.splice(key,1);
                if (sessionStorage.getItem('user') === '0') {
                  this.$emit('updateAppMeal',2,1)
                } else {
                  this.$emit('updateAppMeal',2,2)
                }
              } else {
                const content = res.data.msg
                this.$Modal.error({
                  title: "删除失败",
                  content: content
                });
              }
            });
          },
        });
      },
//      this is a methods  add meal
      addMeal () {
        this.id = -1;
        this.flag = true;
//        重置输入框
        this.items.forEach(item => {
          item.modal = '';
        })
        this.mealForm = true;
      },
      publicData (url,val,id,type) {
        let input=document.getElementsByClassName('memberInput');
        if(this.items[0].modal&&this.items[1].modal&&this.items[2].modal){
          this.post(url,{
            type,
            price: this.items[0].modal,
            year_num: this.items[1].modal,
//          app_num: this.items[2].modal,
            type_auto: val,
            type_ssh: 2,
            id
          }).then(res=>{
            if(res.data.code === 10000){
              if (sessionStorage.getItem('user') === '0') {
                this.$emit('updateMealList', 2, 1)
              } else {
                  this.$emit('updateMealList',2,2)
              }
              this.mealForm = false;
            }else{
              this.$Modal.error({
                title: "添加失败",
                content: res.data.msg
              });
            }
          })
        }else{
          if(!this.items[0].modal.trim()){
            input[0].style.borderColor = '#e46751';
            this.items[0].show = true;
          }
          if(!this.items[1].modal.trim()) {
            input[1].style.borderColor = '#e46751';
            this.items[1].show = true;
          }
          if(!this.items[2].modal.trim()){
            input[2].style.borderColor = '#e46751';
            this.items[2].show = true;
          }
        }
      },
//      取消检测
      cancelCheck (val) {
        this.items[val].show = false;
      },
//      检测
      check (val) {
        if(this.items[val].modal.trim()){
          this.items[val].show = false;
          this.items[val].right = true;
          event.target.style.borderColor = '#EAEAEA'
        }else{
          this.items[val].show = true;
          this.items[val].right = false;
          event.target.style.borderColor = '#e46751'
        }
      },
      save () {
        if (sessionStorage.getItem('user') === '0') {
//          超管保存
          if (this.id === -1) {
            this.publicData('system/Agency/app_setting',1,'',this.type)
          } else {
            this.publicData('system/Agency/update_setting',1,this.id)
          }
        } else {
          if (this.id === -1) {
            this.publicData('system/Agency/app_setting',2,'',this.type)
          } else {
            this.publicData('system/Agency/update_setting',2,this.id)
          }
        }
      },
      cancel () {
        for(let i=0;i<this.items.length;i++){
          this.items[i].show=false;
          this.items[i].right=false;
          this.items[i].modal='';
        }
        let input=document.getElementsByClassName('memberInput');
        for(let j=0;j<input.length;j++){
          input[j].style.borderColor='#EAEAEA';
        }
      },
      enter () {
        event.target.style.borderColor='#6098FF'
      },
      out (val) {
        switch (val){
          case 0:
            this.check(0);
            break;
          case 1:
            this.check(1);
            break;
          case 2:
            this.check(2);
            break;
          case 3:
            this.check(3);
            break;
        }
      },
      input (val) {
        switch (val){
          case 0:
            this.cancelCheck(0);
            break;
          case 1:
            this.cancelCheck(1);
            break;
          case 2:
            this.cancelCheck(2);
            break;
          case 3:
            this.cancelCheck(3);
            break;
        }
      },
    },
    props: ["mealSort","mealList"],
    watch: {
      mealSort (val) {
        return val;
      },
      mealList (val) {
        return val;
      }
    },
    filters:{
      nameFilter (val) {
        switch (val){
          case 1:
            return "电商小程序";
          case 2:
            return "预约小程序";
          case 3:
            return "酒店小程序";
        }
      },
      mealName(val){
        switch (val){
          case 1:
            return "电商套餐";
          case 2:
            return "预约套餐";
          case 3:
            return "酒店套餐";
        }
      }
    },
    mounted(){

    },
    components:{bread}
  }
</script>
<style scoped>
  p{
    padding: 9px 0;
    display: flex;
    align-items: center;
  }
  p>span{
    display: inline-block;
    width: 145px;
    text-align: right;
    margin-right: 20px
  }
  p>strong{
    display: flex;
    align-items: center;
    color: #e46751;
    font-weight: 400;
    margin-left: 10px;

  }
  .must{
    font-size: 15px;
    color: red;
    margin-right: 10px;
  }

  .btn{
    padding: 20px 168px;
  }
  .save{
    display: inline-block;
    padding: 8px 35px;
    background-color: #6098FF;
    border-radius:5px;
    color: white;
  }
  .cancel{
    margin-left: 15px;
    display: inline-block;
    padding: 7px 34px;
    border: 1px solid #777777;
    border-radius: 5px;
    color:#777777;
  }
  .cancel:hover{
    background:#799EE3 ;
    color: white;
    text-decoration:none;
    border: none;
  }
  input{
    display: inline-block;
    padding: 8px 10px;
    border: 1px solid #EAEAEA;
    width: 400px;
    border-radius: 5px;
    background-color: #FFFFFF;
  }
  .add{
    display: inline-block;
    margin-right: 0.5em;
    font-size: 120%;
    vertical-align: middle;
  }
  .select_type{
    display: inline-block;
    padding: 8px 1em;
    width: 400px;
    border: 1px solid #EAEAEA;
    border-radius: 5px;
    background-color: #FFFFFF;
  }
  .mealList{
    padding: 1em 0;
  }
</style>



