<template>
  <div class="userList">
    <bread></bread>
    <Tabs type="card" @on-click="handleTab">
      <TabPane label="代理商" name="1" v-if="power === '0'">
        <agentPower :mealSort="value" :mealList="list" @updateAppMeal="getAppSorts" @updateMealList="getMealList" ref="agent"></agentPower>
      </TabPane>
      <TabPane label="普通用户" name="2">
        <memberPower :mealSort="value" :mealList="list" @updateAppMeal="getAppSorts" @updateMealList="getMealList"></memberPower>
      </TabPane>
    </Tabs>
  </div>
</template>
<script>
  import bread from '../bread.vue'
  import agentPower from './agentPower.vue'
  import memberPower from './memberPower.vue'
export default{
  data(){
    return {
      value: [],
      list: [],
      power:''
    }
  },
  methods:{
/*   this is a method get appSort */
//type 1代理商 2 用户 user 1超管 2代理商
    getAppSorts (type,user) {
      this.post("system/Agency/get_app",{
        type_auto: user,
        type_ssh: type
      }).then(res => {
        if (res.data.code === 10000){
          let mealList = res.data.data;
          let mealSort = [];
          for (let item in mealList) {
            mealSort.push(mealList[item]);
          }
          this.value = mealSort;
        } else {
          this.$Modal.error({
            title: "获取失败",
            content: res.data.msg
          });
        }
      })
    },
    /* this is a methods  get mealList*/
    getMealList (type,user = 1) {
      this.list = [];
      this.post("system/Agency/get_package",{
        type_auto: user,
        type_ssh: type
      }).then(res => {
        if (res.data.code === 10000){
          this.list = res.data.data;
        } else {
          this.$Modal.error({
            title: "获取失败",
            content: res.data.msg
          });
        }
      })
    },
    handleTab (val) {
        if (val === '1') {
          this.getAppSorts(val,1)
          this.getMealList(val,1);
        } else {
          this.getAppSorts(val,1)
          this.getMealList(val,1);
        }
    }
  },
  created(){
    this.power = sessionStorage.getItem('user');
    if(this.power === '0'){
      this.getAppSorts(1,1);
      this.getMealList(1,1);
    } else {
      this.getAppSorts(2,2);
      this.getMealList(2,2);
    }
  },
  components:{bread,agentPower,memberPower}
}
</script>
<style scoped>
  .userList{
    width: 98%;
    margin: 0 auto;
  }
</style>



